@import url(https://fonts.googleapis.com/css2?family=Questrial&display=swap);
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  color: inherit; /*for <button> and <a>*/
}

:root {
  --margin-y-md: 3rem;
  --box-shad-md: 1rem 2rem 2rem rgba(0, 0, 0, 0.19),
    0 6px 6px rgba(0, 0, 0, 0.23);
  --color-green: rgb(0, 255, 0);
}

html {
  font-size: 62.5%;
}

@media only screen and (max-width: 1200px) {
  html {
    font-size: 56.25%;
  }
}
@media only screen and (max-width: 900px) {
  html {
    font-size: 50%;
  }
}
@media only screen and (max-width: 600px) {
  html {
    font-size: 43.75%;
  }
}

#root {
  height: 100vh;
  overflow: hidden;
}

.app {
  height: 100%;
  padding: 1.5rem 4rem;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-family: 'Questrial';
  color: white;
  text-align: center;
  transition: background-color 0.9s ease-in;
}

@media only screen and (max-width: 600px) {
  .app {
    padding: 0 3rem;
  }
}

/*Landing Page*/
.heading-primary {
  font-size: 5rem;
  letter-spacing: 5px;
  margin-bottom: 3rem;
  margin-bottom: var(--margin-y-md);
  -webkit-animation: fadeIn 2s 1s backwards, fromTop 2s 1s backwards;
          animation: fadeIn 2s 1s backwards, fromTop 2s 1s backwards;
}

.heading-tertiary {
  font-size: 2.5rem;
  letter-spacing: 1.5px;
  margin-bottom: 3rem;
  margin-bottom: var(--margin-y-md);
  line-height: 3rem;
  -webkit-animation: fadeIn 2s 2.6s backwards;
          animation: fadeIn 2s 2.6s backwards;
}

.loader {
  margin: 0 auto;
  display: -webkit-flex;
  display: flex;
  transition: opacity 0.3s ease-in;
}

.loading-circle {
  border-radius: 50%;
  height: 0.8rem;
  width: 0.8rem;
  margin: 0 1rem;
  background: white;
  -webkit-animation: bounce 0.5s ease-in infinite;
          animation: bounce 0.5s ease-in infinite;
}

.loading-circle:nth-of-type(2) {
  -webkit-animation-delay: 0.1s;
          animation-delay: 0.1s;
}

.loading-circle:nth-of-type(3) {
  -webkit-animation-delay: 0.2s;
          animation-delay: 0.2s;
}

.loading-text {
  margin-top: 3rem;
  margin-top: var(--margin-y-md);
  font-size: 4rem;
}

.btn-cta {
  margin: 0 auto;
  font-size: 6rem;
  min-width: 15%;
  background: black;
  cursor: pointer;
  border: none;
  -webkit-animation: fromBottom 1.2s 3.8s ease-out backwards;
          animation: fromBottom 1.2s 3.8s ease-out backwards;
  transition: initial;
  padding: 0.5rem 4rem;
  border-radius: 10rem;
  transition: all 0.3s;
  border: solid 5px red;
  color: red;
}

.btn-cta:hover,
.btn-cta:active,
.btn-cta:focus {
  outline: 0;
  background: black;
  color: rgb(0, 255, 0);
  color: var(--color-green);
  border-color: rgb(0, 255, 0);
  border-color: var(--color-green);
  -webkit-transform: scale(0.98);
          transform: scale(0.98);
}

.btn-cta .icon-svg {
  vertical-align: -0.8rem;
}

.instructional-text {
  font-size: 2.5rem;
  margin-top: 3rem;
  margin-top: var(--margin-y-md);
  -webkit-animation: fromBottom 1.2s 4.8s ease-out backwards;
          animation: fromBottom 1.2s 4.8s ease-out backwards;
}

.user-instruction {
  color: rgb(0, 255, 0);
  color: var(--color-green);
}

.instructional-text.desktop {
  font-size: 2rem;
}

@media only screen and (max-width: 600px) {
  .instructional-text.desktop {
    display: none;
  }
}

/*Fetch successful*/
.quote-card {
  background: rgb(255, 235, 235);
  padding: 2rem;
  max-width: 114rem;
  margin: 0 auto;
  border-radius: 1rem;
  box-shadow: 1rem 2rem 2rem rgba(0, 0, 0, 0.19),
    0 6px 6px rgba(0, 0, 0, 0.23);
  box-shadow: var(--box-shad-md);
  -webkit-animation: zoomIn 0.8s ease-out;
          animation: zoomIn 0.8s ease-out;
}

@media only screen and (max-width: 600px) {
  .quote-card {
    padding: 1rem 1rem;
  }
}

.quote-container {
  transition: opacity 1.1s ease-in-out, color 1s ease-in;
}

.fade {
  opacity: 0;
}

.quote-text {
  padding: 1.2rem 1.5rem;
  font-size: 3.6rem;
  font-weight: 600;
  letter-spacing: 1.5px;
  line-height: 5rem;
  max-height: 55vh;
  overflow-y: auto;
  -webkit-user-select: text;
      -ms-user-select: text;
          user-select: text;
}

@media only screen and (max-width: 600px) {
  .quote-text {
    padding: 0.5rem;
    max-height: 40vh;
  }
}

@media only screen and (max-width: 600px) {
  .quote-text {
    font-size: 3rem;
    line-height: 4.2rem;
  }
}

.quote-text .icon-svg {
  vertical-align: -0.5rem;
}

.quote-author {
  font-size: 3.2rem;
  text-align: right;
  font-style: italic;
  margin: 1rem 0 4rem;
  -webkit-user-select: text;
      -ms-user-select: text;
          user-select: text;
}

@media only screen and (max-width: 600px) {
  .quote-author {
    margin-bottom: 2rem;
    font-size: 2.4rem;
  }
}

.buttons-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.buttons-container .icon-svg {
  vertical-align: -5px;
}

.btn-socialMedia,
.btn-shuffle {
  font-size: 3.5rem;
  box-shadow: 2px 4px 4px rgba(0, 0, 0, 0.19), 0 1px 1px rgba(0, 0, 0, 0.23);
  margin: 3px;
  border-radius: 0.5rem;
  border: 0;
  padding: 0;
  opacity: 0.75;
  transition: background-color 1s ease-in, -webkit-transform 0.2s;
  transition: transform 0.2s, background-color 1s ease-in;
  transition: transform 0.2s, background-color 1s ease-in, -webkit-transform 0.2s;
}

.link-socialMedia {
  display: block;
  padding: 0.9rem 1.1rem;
}

.btn-shuffle .icon-svg {
  padding: 0.9rem 1.1rem;
}

.btn-socialMedia:focus,
.btn-shuffle:focus {
  outline: 0;
}

/*for touch devices*/
.btn-socialMedia:active,
.btn-shuffle:active {
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}

/*for mouse devices*/
@media only screen and (hover: hover) {
  .btn-socialMedia:hover,
  .btn-shuffle:hover {
    box-shadow: 1.5px 3px 3px rgba(0, 0, 0, 0.19), 0 1px 1px rgba(0, 0, 0, 0.23);
    cursor: pointer;
    opacity: 1;
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
  }
  .btn-socialMedia:active,
  .btn-shuffle:active {
    -webkit-transform: none;
            transform: none;
    -webkit-transform: initial;
            transform: initial;
  }
}

/*Footer*/
.footer {
  font-size: 2.2rem;
  margin-top: 3rem;
  margin-top: var(--margin-y-md);
}

@media only screen and (max-width: 600px) {
  .footer {
    font-size: 2.5rem;
  }
}

/*Animations*/
@-webkit-keyframes bounce {
  0%,
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  50% {
    -webkit-transform: translateY(-0.6rem);
            transform: translateY(-0.6rem);
  }
}
@keyframes bounce {
  0%,
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  50% {
    -webkit-transform: translateY(-0.6rem);
            transform: translateY(-0.6rem);
  }
}

@-webkit-keyframes fromBottom {
  0% {
    opacity: 0;
    -webkit-transform: translateY(3rem);
            transform: translateY(3rem);
  }

  80% {
    -webkit-transform: translateY(-0.3rem);
            transform: translateY(-0.3rem);
  }
}

@keyframes fromBottom {
  0% {
    opacity: 0;
    -webkit-transform: translateY(3rem);
            transform: translateY(3rem);
  }

  80% {
    -webkit-transform: translateY(-0.3rem);
            transform: translateY(-0.3rem);
  }
}

@-webkit-keyframes fromTop {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-3rem);
            transform: translateY(-3rem);
  }

  80% {
    -webkit-transform: translateY(0.3rem);
            transform: translateY(0.3rem);
  }
}

@keyframes fromTop {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-3rem);
            transform: translateY(-3rem);
  }

  80% {
    -webkit-transform: translateY(0.3rem);
            transform: translateY(0.3rem);
  }
}

@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
}

@-webkit-keyframes zoomIn {
  from {
    -webkit-transform: scale(0.7);
            transform: scale(0.7);
    opacity: 0;
  }
}

@keyframes zoomIn {
  from {
    -webkit-transform: scale(0.7);
            transform: scale(0.7);
    opacity: 0;
  }
}

